<template>
		<div id="app">
				<div id="app-home">
						<header>
					  <div class="home">
						  	<router-link to="/nav">
						  			<img src="../src/assets/00-logo.png" alt="">
						  	</router-link>
					  </div>
					  <div class="title">热狗</div>
					  <div class="iconfont">
					  		<router-link to="/login">&#xe67e</router-link>
					  </div>
			  </header>
				</div>
			  <router-view/>
		</div>
</template>

<script>
		export default {
		  	name: 'app'
		}
</script>

<style lang="scss">
		$sc:50;
		
		@font-face {
			  font-family: 'iconfont';
			  src: url('../static/font/iconfont.eot');
			  src: url('../static/font/iconfont.eot?#iefix') format('embedded-opentype'),
			  url('../static/font/iconfont.woff') format('woff'),
			  url('../static/font/iconfont.ttf') format('truetype'),
			  url('../static/font/iconfont.svg#iconfont') format('svg');
		}
		.iconfont{
			  font-family:"iconfont" !important;
			  font-style:normal;
			  -webkit-font-smoothing: antialiased;
			  -webkit-text-stroke-width: 0.2px;
			  -moz-osx-font-smoothing: grayscale;
		}
		
		html{
			  width: 100%;
			  height: 100%;
			  body{
				    @extend html;
				    margin: 0;
				    #app{
					      @extend html;
					      display: flex;
					      flex-direction: column;					      
					      #app-home{
					      		top: 0;
					      		header{
								        width: 100%;
								        height: 100/$sc+rem;
								        font-size: 66/$sc+rem;
								        color: #fff;
								        background: #ffda44;
								        top: 0;
								        padding: 12/$sc+rem 24/$sc+rem;
								        box-sizing: border-box;
								        display: flex;
								        justify-content: space-between;
								        a{
									          text-decoration: none;
									          color: #fff;
									          display: block;
								        }
								        .home{
									          width: 76/$sc+rem;
									          height: 76/$sc+rem;
									          a{
										            @extend .home;
										            img{
											              @extend .home;
											              border-radius: 50%;
										            }
									          }
								        }
								        .title{
									          font-size: 50/$sc+rem;
									          font-weight: 600;
								        }
					      		}
					      }
				    }
			  }
		}
</style>



