<template>
	<div id="set">
		<div class="set-header">
			<router-link to="/user">返</router-link>
			设置
		</div>
		<div class="set-list">
			<ul>
				<li>
					<span>修改资料</span>
					<span>图标</span>
				</li>
				<li>
					<span>阅读偏好</span>
					<span>图标</span>
				</li>
				<li>
					<span>字号设置</span>
					<span>中 图标</span>
				</li>
				<li>
					<span>视频自动播放</span>
					<span>仅wifi 图标</span>
				</li>
				<li>
					<span>离线阅读</span>
					<span>图标</span>
				</li>
				<li>
					<span>推送设置</span>
					<span>图标</span>
				</li>
				<li>
					<span>清除缓存</span>
					<span>4.6MB</span>
				</li>
				<li>
					<span>关于热狗</span>
					<span>图标</span>
				</li>
			</ul>
			<div class="out">
				<router-link to="/login">
					<input type="submit" value="退出登录" />
				</router-link>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
  
	}
</script>

<style lang="scss" scoped>
  $sc:50;

html{
    width: 100%;
    height: 100%;
    body{
        @extend html;
        margin: 0;
        #set{
            @extend html;
            display: flex;
            flex-direction: column;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            background: #f6f2e3;
            .set-header{
                width: 100%;
                height: 100/$sc+rem;
                line-height: 100/$sc+rem;
                text-align: center;
                font-size: 50/$sc+rem;
                color: #fff;
                position: relative;
                background: #ffda44;
                a{
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 30/$sc+rem;
                    color: #fff;
                    font-size: 30/$sc+rem;
                    text-decoration: none;
                }
            }
            .set-list{
                @extend html;
                padding: 40/$sc+rem;
                box-sizing: border-box;
                ul{
                    margin: 0;
                    padding: 0;
                    list-style: none;
                    li{
                        width: 100%;
                        height: 90/$sc+rem;
                        line-height: 90/$sc+rem;
                        border-bottom: 1/$sc+rem solid #ddd;
                        font-size: 22/$sc+rem;
                        display: flex;
                        justify-content: space-between;
                        
                    }
                }
            }
            .out{
                width: 400/$sc+rem;
                height: 70/$sc+rem;
                margin: 50/$sc+rem auto 0;
                input{
                    width: 400/$sc+rem;
                    height: 70/$sc+rem;
                    outline: none;
                    border: 1/$sc+rem solid #ffda44;
                    background: #ffda44;
                    color: #fff;
                }
            }
            
        }
    }
}

</style>


