<template>
	<div id="news">
		<ul id="ul">
			
           
        </ul>
	</div>
</template>

<script>
	export default{
		
	}
</script>

<script>
	$(function(){
		function fn(){		
			var arr =[]
			var arr1=[]      //w
			var arr2=[]
			for(var i =0;i<aip.result.data.length;i++){
				var obj = {};     
				var obj1 ={};    //w
				var obj2 ={}; 
				obj.title = aip.result.data[i].title;
				obj1.thumbnail_pic_s=aip.result.data[i].thumbnail_pic_s  //w
				obj2.title = aip.result.data[i].url;
				obj.category = aip.result.data[i].category;
				
				obj.date = aip.result.data[i].date;
				obj.name = aip.result.data[i].author_name
				arr.push(obj)
				arr1.push(obj1)   //w
				arr2.push(obj2)
			}
//			console.log(arr2) 
			var uls =$("#ul")
			for(var i=0;i<arr.length;i++){							
				var html='<li><div><h4>' + arr[i].title + '</h4></div></li>';     //创建ul下的子级
				var ri  ='<div><img></div>'
				var divL=$("#ul>li:first-child>div").addClass("lee-left")   //w 给左边div加class名
				var lis =$("#ul>li")
				uls.prepend(html);
			}   
			
			var divL=$("#ul>li:first-child>div").addClass("lee-left")
				
			///////////左边

			var divR=$(".lee-left").after("<div></div>")   //r    																							  
			var divLC =$("li>div:last-child")																							  
			divLC.addClass("lee-right");
			var Img =divLC.append("<img src=''>")   //thumbnail_pic_s    
			var lis =$("#ul>li")  
			for(var i=0;i<arr.length;i++){		   //添加图片
				$("#ul li").eq(i).find(".lee-right>img").attr("src",aip.result.data[i].thumbnail_pic_s)				
			}
			
			var Hsize=document.documentElement.style.fontSize
//			console.log(Hsize)
//			$('li').css('width','100%').css('height','237/Hsize+rem')
		}
		fn()
	})
</script>
