<template>
	<div id="register">
		<div class="header">
			<router-link to="/nav">返</router-link>
			注册热狗
		</div>
		<div class="section">
			<input type="text" placeholder="账号" />
			<input type="password" placeholder="密码" />
		</div>
		<div class="sub">
			<router-link to="/user">
				<input type="submit"  value="注册"/>
			</router-link>
		</div>
		<p><input type="checkbox" />阅读并代表同意<span>《热狗服务条款和隐私条款》</span></p>
	</div>
</template>

<script>
	export default {
		
	}
</script>

<style lang="scss" scoped>
$sc:50;

html{
    width: 100%;
    height: 100%;
    body{
        @extend html;
        margin: 0;
        #register{
            @extend html;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            display: flex;
            flex-direction: column;
            box-sizing: border-box;
            background: #fff;
            .header{
                width: 100%;
                height: 100/$sc+rem;
                line-height: 100/$sc+rem;
                text-align: center;
                font-size: 50/$sc+rem;
                color: #fff;
                background: #ffda44;
                a{
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 24/$sc+rem;
                    font-size: 24/$sc+rem;
                    color: #fff;
                }
            }
            .section{
                width: 100%;
                margin-top: 20/$sc+rem;
                padding: 0 60/$sc+rem 0;
                box-sizing: border-box;
                input{
                    width: 100%;
                    height: 50/$sc+rem;
                    outline: none;
                    border: none;
                    border-bottom: 1/$sc+rem solid #c4c4c4;
                    font-size: 24/$sc+rem;
                    margin-top: 90/$sc+rem;
                    padding: 0 10/$sc+rem;
                    box-sizing: border-box;
                    &::placeholder{
                        color: #c4c4c4;
                    }
                }
                
            }
            .sub{
                width: 280/$sc+rem;
                height: 73/$sc+rem;
                margin: 60/$sc+rem auto 20/$sc+rem;
                box-sizing: border-box;
                font-size: 24/$sc+rem;
                input{
                    width: 280/$sc+rem;
                    height: 71/$sc+rem;
                    line-height: 71/$sc+rem;
                    text-align: center;
                    background: #ffda44;
                    color: #fff;
                    outline: none;
                    border: 1/$sc+rem solid #ffda44;
                }
            }
            p{
                width: 100%;
                height: 30/$sc+rem;
                line-height: 30/$sc+rem;
                text-align: center;
                font-size: 12/$sc+rem;
                margin: 0;
                position: relative;
                input{
                    margin: 0;
                    position: absolute;
                    top: 2/$sc+rem;
                    left: 105/$sc+rem;
                }
                span{
                    color: dodgerblue;
                }
            }
        }
    }
}

</style>


