<template>
	<div id="user">
		<div class="user-header">
			<div class="top">
				<span><router-link to="/nav">返</router-link></span>
				<span><router-link to="/set">设</router-link></span>
			</div>
			<div class="username">
				<div class="user-img">
					<img src="../assets/touxiang.jpg" alt="" />
				</div>
				<div class="user-msg">
					<p>USERNAME</p>
					<ul>
						<li>
							<span>收藏</span>
							<span>2</span>
						</li>
						<li>
							<span>历史</span>
							<span>3</span>
						</li>
						<li>
							<span>评论</span>
							<span>4</span>
						</li>
					</ul>
				</div>
			</div>
		</div>
		<div class="msg-list">
			<ul>
				<li>
					<span>图标</span>
					我的消息
				</li>
				<li>
					<span>图标</span>
					我的离线
				</li>
				<li>
					<span>图标</span>
					我的关注
				</li>
				<li>
					<span>图标</span>
					我的分享
				</li>
				<li>
					<span>图标</span>
					意见反馈
				</li>
			</ul>
		</div>
		<router-view/>
	</div>
</template>

<script>
	export default {
  
	}
</script>

<style lang="scss" scoped>
  $sc:50;

@mixin fb(){
    display: flex;
    justify-content: space-between;
}
@mixin fc(){
    display: flex;
    flex-direction: column;
}
@mixin ul(){
    margin: 0;
    padding: 0;
    list-style: none;
}
html{
    width: 100%;
    height: 100%;
    body{
        @extend html;
        margin: 0;
        #user{
            @extend html;
            @include fc;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            .user-header{
                width: 100%;
                height: 300/$sc+rem;
                .top{
                    width: 100%;
                    height: 100/$sc+rem;
                    padding: 0 24/$sc+rem;
                    box-sizing: border-box;
                    @include fb;
                    background: #ffda44;
                    span{
                        display: block;
                        width: 50/$sc+rem;
                        height: 100/$sc+rem;
                        line-height: 100/$sc+rem;
                        text-align: center;
                        font-size: 40/$sc+rem;
                        color: #fff;
                        a{
                            color: #fff;
                            text-decoration: none;
                        }
                    }
                }
                .username{
                    width: 100%;
                    height: 200/$sc+rem;
                    padding: 30/$sc+rem;
                    box-sizing: border-box;
                    display: flex;
                    justify-content: space-around;
                    background: #f8efcb;
                    .user-img{
                        width: 140/$sc+rem;
                        height: 140/$sc+rem;
                        img{
                            @extend .user-img;
                            border-radius: 50%;
                        }
                    }
                    .user-msg{
                        width: 350/$sc+rem;
                        p{
                            margin: 0;
                            width: 100%;
                            font-size: 40/$sc+rem;
                            color: #d7ac00;
                        }
                        ul{
                            @include ul;
                            margin-top: 10/$sc+rem;
                            @include fb;
                            li{
                                width: 80/$sc+rem;
                                font-size: 20/$sc+rem;
                                @include fc;
                                span:last-child{
                                    font-weight: 600;
                                }
                            }
                        }
                    }
                }
            }
            .msg-list{
                @extend html;
                background: #f6f2e3;
                padding: 40/$sc+rem;
                box-sizing: border-box;
                ul{
                    @include ul;
                    li{
                        width: 100%;
                        height: 90/$sc+rem;
                        line-height: 90/$sc+rem;
                        border-bottom: 1/$sc+rem solid #ddd;
                        font-size: 22/$sc+rem;
                        &:last-child{
                            border-bottom: none;
                        }
                    }
                }
            }
        }
    }
}

</style>


