<template>
	<div id="nav">
		<nav>
        	<router-link to="/news">新闻</router-link>
        	<router-link to="/v">视频</router-link>
        	<router-link to="/music">音乐</router-link>
        	<router-link to="/novel">小说</router-link>
        	<router-link to="/special">专题</router-link>
        	<router-link to="/funny">奇趣</router-link>
        </nav>
        <router-view/>
	</div>
</template>

<script>
export default {
  
}
</script>

<style lang="scss" scoped>
$sc:50;
html{
    width: 100%;
    height: 100%;
    body{
        @extend html;
        margin: 0;
        #nav{
            @extend html;
            top: 100/$sc+rem;
            overflow-y: auto;
            display: flex;
			flex-direction: column;
            nav{
                width: 100%;
                height: 84/$sc+rem;
                top: 100/$sc+rem;
                background: #f8efcb;
                display: flex;
                justify-content: space-between;
                padding: 0 24/$sc+rem;
                box-sizing: border-box;
                border-bottom: 4/$sc+rem solid #f4f4f4;
                a{
                    width: 52/$sc+rem;
                    height: 100%;
                    line-height: 84/$sc+rem;
                    text-align: center;
                    display: block;
                    font-size: 26/$sc+rem;
                    text-decoration: none;
                    color: #999;
                    &:first-child{
                        color: #000;
                        border-bottom: 4/$sc+rem solid #ffda44;
                        border-radius: 2/$sc+rem;
                    }
                }
            }
        }
    }
}
</style>


