<template>
	<div id="login">
		<div class="header">
			<router-link to="/nav">返</router-link>
			登录热狗
		</div>
		<div class="section">
			<input type="text" placeholder="账号" />
			<input type="password" placeholder="密码" />
		</div>
		<div class="sub">
			<a href="#javascript:;">忘记密码？</a>
			<router-link to="/user">
				<input type="submit"  value="登录"/>
			</router-link>
		</div>
		<div class="logs">
			<p>其他登录方式</p>
			<div>
				<span>微信</span>
				<span>微博</span>
				<span>QQ</span>
			</div>
			<router-link to="/register">
				<a href="###">立即注册</a>
			</router-link>
		</div>
		<router-view/>
	</div>
</template>

<script>
	export default {
		
	}
</script>

<style lang="scss" scoped>
$sc:50;

html{
    width: 100%;
    height: 100%;
    #login{
        @extend html;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        background: #fff;
        .header{
            width: 100%;
            height: 100/$sc+rem;
            line-height: 100/$sc+rem;
            text-align: center;
            font-size: 50/$sc+rem;
            color: #fff;
            background: #ffda44;
            a{
                display: block;
                position: absolute;
                top: 0;
                left: 24/$sc+rem;
                font-size: 24/$sc+rem;
                color: #fff;
            }
        }
        .section{
            width: 100%;
            margin-top: 20/$sc+rem;
            padding: 0 60/$sc+rem 0;
            box-sizing: border-box;
            input{
                width: 100%;
                height: 50/$sc+rem;
                outline: none;
                border: none;
                border-bottom: 1/$sc+rem solid #c4c4c4;
                font-size: 24/$sc+rem;
                margin-top: 90/$sc+rem;
                padding: 0 10/$sc+rem;
                box-sizing: border-box;
                &::placeholder{
                    color: #c4c4c4;
                }
            }
            
        }
        .sub{
            width: 100%;
            height: 73/$sc+rem;
            margin: 60/$sc+rem 0;
            padding: 0 60/$sc+rem 0;
            box-sizing: border-box;
            display: flex;
            justify-content: space-between;
            font-size: 24/$sc+rem;
            a,input{
                display: block;
                width: 280/$sc+rem;
                height: 71/$sc+rem;
                line-height: 71/$sc+rem;
                text-align: center;
            }
            a{
                border: 1/$sc+rem solid #d6d6d6;
                text-decoration: none;
                color: #666;
            }
            input{
                background: #ffda44;
                color: #fff;
                outline: none;
                border: 1/$sc+rem solid #ffda44;
            }
        }
        .logs{
            width: 100%;
            padding: 0 60/$sc+rem 0;
            box-sizing: border-box;
            p{
                margin: 0 auto;
                width: 100%;
                height: 60/$sc+rem;
                line-height: 60/$sc+rem;
                text-align: center;
                font-size: 12/$sc+rem;
                color: #666;
                border-top: 1/$sc+rem solid #999;
            }
            div{
                width: 100%;
                height: 100/$sc+rem;
                margin-top: 20/$sc+rem;
                display: flex;
                justify-content: space-between;
                span{
                    display: block;
                    width: 100/$sc+rem;
                    height: 100/$sc+rem;
                    border: 1/$sc+rem solid red;
                }
            }
            a{
                display: block;
                width: 100/$sc+rem;
                height: 20/$sc+rem;
                text-decoration: none;
                color: dodgerblue;
                font-size: 14/$sc+rem;
                margin: 100/$sc+rem auto 0;
            }
        }
    }
}

</style>


